import { Container, Stack, Box } from "@mui/material";
import Select from "../../components/Select";
import TextField from "../../components/TextField";
import CheckboxLabels from "../../components/CheckBox";
import Button from "../../components/Button";
import Grid from "@mui/material/Unstable_Grid2";

const data2 = [
  { label: "", id: "mv" },
  { label: "+ (MEVO PLUS)", id: "mvp" },
  { label: " (MEVO Range)", id: "mvr" },
];

export default function Flight() {
  const onSubmit = (event) => {
    event.preventDefault();
  };

  return (
    <Container sx={{ p: { xs: 2 }, pt: 1 }}>
      <Box component="form" onSubmit={onSubmit}>
        <Grid container spacing={1}>
          <Grid xs={12}>
            <Select
              data={data2}
              placeholder=" ()"
              id="sub_select1"
              required
            />
          </Grid>
          <Grid xs={12}>
            <TextField id="key" placeholder=" ()" required />
          </Grid>
          <Grid xs={12}>
            <TextField id="name" placeholder="()" required />
          </Grid>
          <Grid xs={12}>
            <TextField id="tel" placeholder="()" required />
          </Grid>

          <Grid xs={12}>
            <CheckboxLabels
              modal={{
                label: "",
                title: "  ",
                substance: `     <br><br>
			        , 
			<span class="inline_block">     .</span>
			<br><br>

			1.     <br>
			-     <br>
			-        <br><br>

			2.  <br>
			- , ,  <br><br>

			3.     <br>
			-      `,
              }}
              //link={{ text: "test", href: "/" }}
              label="[]           ."
            />
          </Grid>
        </Grid>
        <Stack
          spacing={1}
          direction="row"
          sx={{ mt: 2, display: "flex", justifyContent: "center" }}
        >
          <Button></Button>
          <Button type="submit" variant="contained">
            
          </Button>
        </Stack>
      </Box>
    </Container>
  );
}
