import { useState } from "react";
import { Container, Stack, Box } from "@mui/material";
import Select from "../../components/Select";
import TextField from "../../components/TextField";
import CheckboxLabels from "../../components/CheckBox";
import Button from "../../components/Button";
import Grid from "@mui/material/Unstable_Grid2";
const data1 = [
  { label: "  ", id: "showgolf" },
  { label: " ", id: "flight1" },
  { label: " ", id: "flight2" },
  { label: " ", id: "academy1" },
  { label: " ", id: "academy2" },
  { label: " ", id: "sin" },
];
const data2 = [
  { label: "", id: "mv" },
  { label: "+ (MEVO PLUS)", id: "mvp" },
  { label: " (MEVO Range)", id: "mvr" },
];
const data3 = [
  { label: "", id: "new" },
  { label: " ", id: "indoor" },
  { label: " ", id: "outdoor" },
];

const data4 = [
  { label: "", id: "kimpo" },
  { label: "", id: "gayang" },
  { label: "", id: "dobong" },
  { label: " ", id: "nonhyun" },
  { label: " ", id: "castle" },
];

export default function Main() {
  const [showFlight1, setShowFlight1] = useState(false);
  const [showFlight2, setShowFlight2] = useState(false);
  const [showAcamemy1, setShowAcademy1] = useState(false);

  const changeSelect = (val) => {
    setShowFlight1(false);
    setShowFlight2(false);
    setShowAcademy1(false);
    switch (val) {
      case "flight1":
        setShowFlight1(true);
        break;
      case "flight2":
        setShowFlight2(true);
        break;
      case "academy1":
        setShowAcademy1(true);
        break;

      default:
        break;
    }
  };

  const onSubmit = (event) => {
    event.preventDefault();
  };

  return (
    <Container sx={{ p: { xs: 2 }, pt: 1 }}>
      <Box component="form" onSubmit={onSubmit}>
        <Grid container spacing={1}>
          <Grid xs={12}>
            <Select
              data={data1}
              placeholder="  ()"
              id="main_select"
              required
              changeSelect={changeSelect}
            />
          </Grid>
          <Grid xs={12}>
            <TextField id="name" placeholder="()" required />
          </Grid>
          <Grid xs={12}>
            <TextField id="tel" placeholder="()" required />
          </Grid>

          {/*   */}
          {showFlight1 && (
            <Grid xs={12}>
              <Select
                data={data2}
                placeholder=" ()"
                id="sub_select1"
                required
              />
            </Grid>
          )}

          {/*   */}
          {showFlight2 && (
            <>
              <Grid xs={12}>
                <Select
                  data={data3}
                  placeholder="  ()"
                  id="sub_select2"
                  required
                />
              </Grid>
              <Grid xs={12}>
                <TextField
                  id="storeRegion"
                  placeholder="  ()"
                />
              </Grid>
              <Grid xs={12}>
                <TextField id="storeSize" placeholder="()" />
              </Grid>
            </>
          )}

          {/*   */}
          {showAcamemy1 && (
            <>
              <Grid xs={12}>
                <Select
                  data={data4}
                  placeholder=" ()"
                  id="sub_select2"
                  required
                />
              </Grid>
            </>
          )}

          <Grid xs={12}>
            <TextField id="email" placeholder="()" />
          </Grid>

          <Grid xs={12}>
            <TextField id="memo" placeholder=" ()" multiline />
          </Grid>
          <Grid xs={12}>
            <CheckboxLabels
              modal={{
                label: "",
                title: "  ",
                substance: `     <br><br>
			        , 
			<span class="inline_block">     .</span>
			<br><br>

			1.     <br>
			-     <br>
			-        <br><br>

			2.  <br>
			- , , <br><br>

			3.     <br>
			-      `,
              }}
              //link={{ text: "test", href: "/" }}
              label={
                <Box component={"span"} sx={{ letterSpacing: -0.8 }}>
                  []         
                   .
                </Box>
              }
            />
          </Grid>
        </Grid>
        <Stack
          spacing={1}
          direction="row"
          sx={{ mt: 2, display: "flex", justifyContent: "center" }}
        >
          <Button></Button>
          <Button type="submit" variant="contained">
            
          </Button>
        </Stack>
      </Box>
    </Container>
  );
}
